@import-normalize;
@font-face {
  font-family: "Proxima Nova";
  src: url("../../assets/fonts/ProximaNova-Regular.woff2") format("woff2"), url("../../assets/fonts/ProximaNova-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Baskerville Display PT";
  src: url("../../assets/fonts/BaskervilleDisplayPT-Bold.woff2") format("woff2"), url("../../assets/fonts/BaskervilleDisplayPT-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  height: 100%;
  scroll-behavior: smooth;
}

ul {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
}

p, h2, h3, h1 {
  margin-top: 0;
  margin-bottom: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

button {
  padding: 0;
  font-family: inherit;
  font-size: 12px;
  border: 0;
  cursor: pointer;
  background-color: transparent;
}

textarea {
  border: none;
  font-size: inherit;
  font-family: inherit;
  appearance: none;
  border-radius: 0;
  outline: none;
}

fieldset {
  margin: 0;
  padding: 0;
  border: 0;
}

legend {
  padding: 0;
}

input {
  border: 0;
  font-family: inherit;
}
input:focus-visible {
  outline: none;
}
input[type=number]::-webkit-outer-spin-button, input[type=number]::-webkit-inner-spin-button {
  appearance: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

body {
  font-family: "Proxima Nova", Arial, Helvetica, sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 100%;
  color: #000;
  margin: 0;
  padding: 0;
}
body.overflow {
  overflow: hidden;
  padding-right: 17px;
}
@media (max-width: 1024px) {
  body.overflow {
    padding-right: 0;
  }
}

.wrapper {
  position: relative;
  overflow: hidden;
  padding-top: 60px;
}
@media screen and (max-width: 767px) {
  .wrapper {
    padding-top: 50px;
  }
}

.container {
  max-width: 1460px;
  width: 100%;
  padding: 0 20px;
  margin: 0 auto;
}

.title {
  font-family: "Baskerville Display PT";
  font-weight: 700;
  font-size: 24px;
  line-height: 150%;
  line-height: 36px;
  letter-spacing: 1.2px;
  margin-bottom: 13px;
  text-transform: uppercase;
}

h1.title {
  margin-bottom: 50px;
}

.center {
  text-align: center;
}

section {
  margin-bottom: 50px;
}

.footer {
  height: 200px;
  background-color: #000;
}

@media screen and (max-width: 1024px) {
  .block-text {
    margin-bottom: 0;
    margin-left: -20px;
    margin-right: -20px;
  }
}
@media screen and (max-width: 1024px) {
  .block-text__first, .block-text:last-of-type {
    margin-bottom: 50px;
  }
}
.block-text__inner {
  display: flex;
}
@media screen and (max-width: 1024px) {
  .block-text__inner {
    flex-direction: column;
    align-items: center;
  }
}
.block-text__content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  max-width: 50%;
  width: 100%;
  padding-right: 55px;
  padding-left: 55px;
}
@media screen and (max-width: 1024px) {
  .block-text__content {
    max-width: 100%;
    margin-bottom: 50px;
    padding-right: 20px;
    padding-left: 20px;
  }
}
@media screen and (max-width: 767px) {
  .block-text__content-title {
    max-width: 319px;
  }
}
.block-text__text {
  line-height: 162.5%;
  letter-spacing: 0.8px;
}
.block-text__text:not(:last-child) {
  margin-bottom: 10px;
}
.block-text__content-img {
  width: 50%;
  object-fit: cover;
  flex-shrink: 0;
}
@media screen and (max-width: 1024px) {
  .block-text__content-img {
    margin-bottom: 50px;
    width: 100%;
  }
}
.block-text .left .block-text__content {
  order: 1;
}

.title-section {
  margin-bottom: 42px;
}
.title-section:has(.title-section__link) {
  margin-bottom: 105px;
}
.title-section:has(.title-section__link) .title-section__text {
  margin-bottom: 30px;
}
.title-section:not(:has(.title-section__link)) .title-section__text {
  text-transform: uppercase;
}
@media screen and (max-width: 767px) {
  .title-section {
    margin-bottom: 50px;
  }
}
.title-section__inner {
  max-width: 600px;
  width: 100%;
  margin: 0 auto;
}
.title-section__link {
  position: relative;
  padding-bottom: 5px;
  letter-spacing: 0.8px;
  transition: 0.3s ease;
}
.title-section__link::after {
  content: "";
  position: absolute;
  display: block;
  width: 100%;
  height: 1px;
  top: 100%;
  left: 0;
  background-color: #000;
}
.title-section__link:hover {
  opacity: 0.7;
}
.title-section__text {
  line-height: 162.5%;
  letter-spacing: 0.8px;
}
@media screen and (max-width: 767px) {
  .title-section__title {
    max-width: 319px;
    margin-left: auto;
    margin-right: auto;
  }
}

.block-image__img {
  width: 100%;
  object-fit: cover;
}
@media screen and (max-width: 1024px) {
  .block-image {
    margin-right: -20px;
    margin-left: -20px;
  }
}

